.noise {
  background-image: url(data:image/png;base64,);
  animation: animated-noise 0.5s step-end infinite;
}

@keyframes animated-noise {
  0% {
    background-position: 41% 64%;
  }
  25% {
    background-position: 56% 13%;
  }
  50% {
    background-position: 74% 92%;
  }
  75% {
    background-position: 42% 55%;
  }
  100% {
    background-position: 31% 45%;
  }
}
